<template>
  <view class="main-container">
    <view class="flex-between">
      <view class="flex">
        <view class="title-img">
          <image class="img" src="https://cdn-static.papa.com.cn/social/title.png" alt="" />
        </view>
        <view class="data">
          <span class="num">234</span>
          次
          <span class="num">70</span>
          h
        </view>
      </view>
      <view class="header-bg">
        <image class="img" src="https://cdn-static.papa.com.cn/social/service-bg.png" alt="" />
      </view>
    </view>
    <view class="list-item-box" v-for="(item, index) in list" :key="index">
      <ActivityItem :item="item" />
    </view>
    <view v-if="list.length == 0">
      <empty :marginTop="0"></empty>
    </view>
    <view class="button-box" v-if="list.length > 0">
      <uv-button
        class="logout-btn mr-2 flex-1"
        type="primary"
        :plain="true"
        shape="circle"
        size="medium"
        @click="handleViewMore"
      >
        查看更多
      </uv-button>
    </view>
  </view>
</template>

<script>
import { getVoluntaryActivityList } from '@/apis/instructorSite/index'
import ActivityItem from './ServiceStyleItem.vue'
import DefaultAvatar from '@/static/images/avatar.png'
import Medal from '@/static/images/medal.png'

export default {
  components: {
    ActivityItem,
  },
  data() {
    return {
      defaultImg: DefaultAvatar,
      medalImg: Medal,
      list: [],
    }
  },
  created() {
    this.getStyleList()
  },
  onLoad(options) {},
  methods: {
    async getStyleList() {
      let res = await getVoluntaryActivityList({ page: 1, size: 2, sort: 2 })
      if (res.code == 200) {
        let dealData = res.data.list.map((item) => {
          let formatObj = {
            addressFormat: item.province_str + item.county_str + item.city_str + item.address,
            startTime: this.$dayjs(item.c_time * 1000).format('YYYY-MM-DD HH:mm:ss'),
          }
          return {
            ...formatObj,
            ...item,
          }
        })
        this.list = dealData
      } else {
        this.$toast(res.message)
      }
    },
    handleViewMore() {
      uni.navigateTo({
        url: '/pages-sub/activityList/index',
      })
    },
  },
}
</script>

